import { useContext } from 'react';
import { Link } from 'react-router-dom';

import style from './Header.module.css';
import logo from '../assets/images/logo_site.png';

import LangContext from '../context/lang';

const navElements = [
    { href: '/', text: 'MAIN' },
    { href: '/parish', text: 'PARISH' },
    { href: '/prior', text: 'PRIOR' },
    { href: '/sisterhood', text: 'SISTERHOOD' },
    { href: '/schedule_liturgy', text: 'SCHEDULE OF LITURGY' },
    { href: '/orthodox_faith', text: 'ORTHODOX FAITH' },
    { href: '/contacts', text: 'CONTACTS' },
];

const Header = () => {
    const { lang, setLang } = useContext(LangContext);
    return (
        <header>
            <div className={style.HeaderTop}>
                <div className={style.HeaderTop__Box}>
                    <Link to='/' className={style.HeaderTop__Brand}>
                        <img src={logo} alt='Holy Trinity Russian Orthodox Church' />
                    </Link>
                    <nav>
                        <ul className={style.HeaderTopNav__Items}>
                            {navElements.map(element => (
                                <li className={style.HeaderTopNav__Item}>
                                    <Link to={element.href}>{element.text}</Link>
                                </li>
                            ))}
                        </ul>
                    </nav>
                </div>
            </div>
            <div className={style.HeaderBottom}>
                <div className={style.HeaderBottom__Box}>
                    <Link to='/parish/support_us' className={style.SupportUs}>SUPPORT US</Link>
                    <button className={`${style.Languages} ${style.RuLang}`} onClick={() => setLang('ru')}>RU</button>
                    <button className={`${style.Languages} ${style.EnLang}`} onClick={() => setLang('en')}>EN</button>
                </div>
            </div>
        </header>
        )}

export default Header;